.welcome-container{
    display: flex;    
    background: linear-gradient(4deg, rgba(0, 172, 193, 0.00) 12.42%, rgba(0, 172, 193, 0.10) 66.32%), #FFF;
    
    height: 100vh;
    padding-top: 100px;
}

.quotes{
    display: inline-block;
    width: 60%;
    margin-left: 80px;
    margin-top: 35px;
}

.contact-us-button{
    display: block;
    margin-top: 70px;
    width: 200px;
    height: 50px;
    background-color: #00ACC1;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    font-size: large;
    color: white;
}

.responsive-image{
    margin-right: 70px;      
    margin-top: -10px;
    width: 680px;
    height: 364px;
}

.typewriter{
    font-size: 2rem;
}

.typewriter span {
    font-size: 2rem;
    min-height: 40px;
    white-space: nowrap;
    overflow: hidden;
    border-right: 0.15em solid black;
    margin-left: 5px;
    width: fit-content;
    animation: blink 0.75s step-end infinite;
    color: #00ACC1;
}

.responsive-design h2{
    position: relative;
    border: 1px solid black;
    padding: 10px;
    width:fit-content;
    border-radius: 20px;
    margin-left: 22%;
    margin-top: 60px;
    border-color: #00ACC1;    
}

@keyframes blink {
    0% {
        border-right-color: black;
    }
    50% {
        border-right-color: transparent;
    }
    100% {
        border-right-color: black;
    }
}


@media (max-width: 1400px) {
    .welcome-container{
        width: 100%;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }

    .quotes{
        width: auto;
        margin: 0;
    }

    .responsive-image{
        width: 100%;
        margin: 0;
        object-fit: contain; 
    }   

    .typewriter span{
        display: block;
        margin: 0;
    }
}

@media (max-width: 500px){
    .quotes{
        width: 80%;
        margin: 0;
    }
}

@keyframes rotateBorder {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }